import { siteConfigs } from "./runtime"

export const updatePageBySiteConfigs = () => {
    if (siteConfigs?.linkSettings) {
        if (siteConfigs.linkOpenOptions?.length > 0) {
            updatePageLinks(siteConfigs.linkOpenOptions);
        } else {
            resetPageLinks();
        }
    } else {
        resetPageLinks();
    }
}

const updatePageLinks = (linkOpenOptions) => {
    document.querySelectorAll("a").forEach(a => {
        if (a.href === window.location.href) {
            return;
        }
        const matchedOption = linkOpenOptions.find(option => {
            try {
                return new RegExp(option.regex).test(a.href);
            } catch {
                return a.href.includes(option.regex);
            }
        });
        if (matchedOption) {
            a.setAttribute("link_target_backup", a.getAttribute("target") || "");
            a.setAttribute("target", matchedOption.target);
        }
    });
}

const resetPageLinks = () => {
    document.querySelectorAll("a[link_target_backup]").forEach(a => {
        a.setAttribute("target", a.getAttribute("link_target_backup"));
        a.removeAttribute("link_target_backup");
    });
}