import { debounce, delay } from "lodash-es";
import { sendRuntimeMessage } from "../../core/browers/runtime";
import { isUsableVideo } from "../../utils/helpers";
import { registerOpenSidePanelEvents, settings, sidePanelOpened } from "./runtime";


export let mainVideo = null;

export const getMainVideo = async (waitTime = 5) => {
    const findMainVideo = () => {
        const videos = Array.from(document.querySelectorAll("video,smp-toucan-player"))
            .filter(video => video.readyState !== 0 && isUsableVideo(video))
            .sort((a, b) => {
                if (a.duration && b.duration && a.duration !== b.duration) return b.duration - a.duration;
                // Sort by video size (descending)
                const rectA = a.getClientRects()[0] || { width: 0, height: 0 };
                const rectB = b.getClientRects()[0] || { width: 0, height: 0 };
                return (rectB.width * rectB.height) - (rectA.width * rectA.height);
            });

        return videos[0] || null;
    };

    let mainVideo = findMainVideo();
    if (mainVideo) return mainVideo;

    return new Promise((resolve) => {
        let attempts = 0;
        const interval = setInterval(() => {
            mainVideo = findMainVideo();
            if (mainVideo || attempts >= waitTime * 10) {
                clearInterval(interval);
                resolve(mainVideo);
            }
            attempts++;
        }, 100);
    });
}


function isSupportPictureInPicture() {
    return ('pictureInPictureEnabled' in document) && (document.pictureInPictureEnabled)
}

function isSupportDocumentPictureInPicture() {
    return ("documentPictureInPicture" in window)
}


function isDocumentPictureInPictureOn() {
    return isSupportDocumentPictureInPicture() && documentPictureInPicture.window !== null;
}

function isPictureInPictureOn() {
    return document.pictureInPictureElement !== null;
}



const registerVideoEvents = (video) => {
    if (isUsableVideo(video)) {
        mainVideo = video;
        sendPictureInPictureInfo();
        sendVideoProgressInfo(video, 'registerVideoEvents');
        handleVideoIntersectionChanged(video);
    }
    video.addEventListener('play', handleVideoPlay);
    video.addEventListener('pause', handleVideoPause);
    video.addEventListener('timeupdate', handleVideoOnTimeupdate);
    video.addEventListener('ended', handleVideoEnded);
    video.addEventListener('volumechange', handleVideoVolumechange);
}

const unregisterVideoEvents = (video) => {
    if (!video) return;
    video.removeEventListener('play', handleVideoPlay);
    video.removeEventListener('pause', handleVideoPause);
    video.removeEventListener('timeupdate', handleVideoOnTimeupdate);
    video.removeEventListener('ended', handleVideoEnded);
    video.removeEventListener('volumechange', handleVideoVolumechange);
}

const handleVideoPlay = (event) => {
    const video = event.target;
    if (!video) return;

    if (maybeAdVideo(video)) {
        return;
    }
    if (isUsableVideo(video)) {
        mainVideo = video;
        registerVideoEvents(video);

        sendPictureInPictureInfo();
        sendVideoProgressInfo(event.target, 'handleVideoPlay');
    }
}
const handleVideoPause = (event) => {
    const video = event.target;
    if (maybeAdVideo(video)) {
        return;
    }
    sendVideoProgressInfo(video, 'handleVideoPause');
}
const handleVideoEnded = (event) => {
    const video = event.target;
    if (maybeAdVideo(video)) {
        return;
    }
    video.removeEventListener('timeupdate', handleVideoOnTimeupdate);

    sendVideoProgressInfo(video, 'handleVideoEnded');
}

const handleVideoVolumechange = (event) => {
    const video = event.target
    sendVideoProgressInfo(video, 'handleVideoVolumechange');
}

const handleVideoOnTimeupdate = (event) => {
    const video = event.target
    if (maybeAdVideo(video)) {
        return;
    }
    debonceSendVideoProgressInfo(video, 'handleVideoOnTimeupdate');
}



const maybeAdVideo = (video) => {
    if (!mainVideo) {
        return false;
    }
    if (mainVideo.paused && !mainVideo.ended && video.muted) {
        //todo 
        return true;
    }
    return false;
}

export function sendPictureInPictureInfo() {

    try {
        sendRuntimeMessage('page_picture_in_picture', {
            pipOn: isPictureInPictureOn() || isDocumentPictureInPictureOn(),
            pipSupport: isSupportPictureInPicture() === true
        })
    } catch (error) {
        console.error("sendPictureInPictureInfo error", error)
    }
}

export const debonceSendVideoProgressInfo = debounce((video, from) => sendVideoProgressInfo(video, from), 250);

export const sendVideoProgressInfo = async (video, from) => {


    if (!video) {
        return
    }
    try {
        if (settings?.enableProgressBar && settings?.playingProgressBar && sidePanelOpened) {
            if (!isUsableVideo(video)) {

                video.removeEventListener('play', handleVideoPlay);
                video.removeEventListener('timeupdate', handleVideoOnTimeupdate);
            }

            await sendRuntimeMessage("playing_timeupdate", {
                from: from,
                loop: video.loop,
                isUsableVideo: isUsableVideo(video),
                isCurrentVideo: mainVideo === video,
                duration: isDisneyplus() ? getDisneyVideoDuration() : video.duration,
                currentTime: Math.floor(video.currentTime),
                paused: video.paused,
                ended: video.ended,
                volume: video.volume,
                muted: video.muted,
                playbackRate: video.playbackRate,
                progress: ((video.currentTime / (isDisneyplus() ? getDisneyVideoDuration() || video.currentTime : video.duration)) * 100).toFixed(2)
            })


        }
    } catch (error) {
        console.error("sendVideoProgressInfo error", error)
    }
}



export const playVideo = async (mainVideo) => {
    if (isNetflix()) {
        window.postMessage({ action: "play_video" }, "https://www.netflix.com");
        return;
    }
    const currentVideo = mainVideo || await getMainVideo();
    currentVideo?.play();
    sendVideoProgressInfo(currentVideo, 'playVideo');
}

export const pauseVideo = async (mainVideo) => {
    if (isNetflix()) {
        window.postMessage({ action: "pause_video" }, "https://www.netflix.com");
        return;
    }

    const currentVideo = mainVideo || await getMainVideo();
    currentVideo?.pause();
    sendVideoProgressInfo(currentVideo, 'pauseVideo');
}


export const seekVideo = async (seconds, mainVideo) => {
    if (isNetflix()) {
        window.postMessage({ action: "seek_video", position: seconds * 1000 }, "https://www.netflix.com");
        return;
    }
    const currentVideo = mainVideo || await getMainVideo();
    if (currentVideo) {
        if (currentVideo.fastSeek) {
            currentVideo.fastSeek(seconds);
        } else {
            currentVideo.currentTime = seconds;
        }
        sendVideoProgressInfo(currentVideo, 'seekVideo');
    }

}

export const volumeVideo = async (vol, mainVideo) => {
    if (isNetflix()) {
        window.postMessage({ action: "volume_video", vol: vol }, "https://www.netflix.com");
        return;
    }
    const currentVideo = mainVideo || await getMainVideo();
    if (currentVideo) {
        currentVideo.volume = vol;
        sendVideoProgressInfo(currentVideo, 'volumeChange');

    }
}
export const playbackRateChange = async (value, mainVideo) => {
    const currentVideo = mainVideo || await getMainVideo();
    if (currentVideo) {
        currentVideo.playbackRate = value;
        sendVideoProgressInfo(currentVideo, 'playbackRateChange');

    }
}
export const switchVideoMute = async (mainVideo) => {
    const currentVideo = mainVideo || await getMainVideo();

    if (currentVideo) {
        currentVideo.muted = !currentVideo.muted;
        sendVideoProgressInfo(currentVideo, 'switchMute');

        setTimeout(() => {
            if (currentVideo.muted !== false && currentVideo.volume === 0) {
                currentVideo.volume = 0.1;
            }
        }, 1000);

    }
}


export async function videoPlayingObserver() {
    mainVideo = await getMainVideo();
    document.removeEventListener('fullscreenchange', handleFullscreenchange, false);
    document.addEventListener('fullscreenchange', handleFullscreenchange, false);

    window.removeEventListener('enterpictureinpicture', sendPictureInPictureInfo);
    window.addEventListener('enterpictureinpicture', sendPictureInPictureInfo);

    window.removeEventListener('leavepictureinpicture', handleLeavepictureinpicture);
    window.addEventListener('leavepictureinpicture', handleLeavepictureinpicture);


    // console.log("videos length", videos.length)
    if (mainVideo) {
        unregisterVideoEvents(mainVideo);
        registerVideoEvents(mainVideo);
        return true;
    } else {
        sendRuntimeMessage("playing_timeupdate", { isUsableVideo: false })
        return false;
    }
}

export const videoMutationObserver = () => {
    const observer = new MutationObserver((mutationsList) => {

        for (let mutation of mutationsList) {

            // console.log("mutation", mutation)
            if (mutation.type === 'attributes' && mutation.target.nodeName === 'VIDEO') {
                // console.log("video attr changed", mutation.attributeName, mutation)
                switch (mutation.attributeName) {
                    case 'src':
                        delay((video) => {
                            unregisterVideoEvents(video)
                            registerVideoEvents(video)
                        }, 3000, mutation.target)

                        break;
                }

            }

            if (mutation.type === 'childList') {
                for (let removeNode of mutation.removedNodes) {
                    if (removeNode.nodeName === 'VIDEO') {
                        // console.log("video removed", mutation.removeNode)
                        unregisterVideoEvents(removeNode)
                    }
                }
                for (let addNode of mutation.addedNodes) {
                    if (addNode.nodeName === 'VIDEO') {
                        // console.log("video added", mutation.addNode)
                        registerVideoEvents(addNode)
                    }
                }
            }
        }
    });
    try {
        observer.observe(document.body, { childList: true, attributes: true, subtree: true });

    } catch (error) {
        console.error(error)
    }
}

const handleLeavepictureinpicture = (event) => {
    event.target.scrollIntoViewIfNeeded();
    sendPictureInPictureInfo();
}

const handleVideoIntersectionChanged = (video) => {
    if (isUsableVideo(video)) {
        initIntersectionObserver().observe(video);
    }
}


let intersectionObserver = null
const initIntersectionObserver = () => {

    if (intersectionObserver) {
        return intersectionObserver;
    }
    intersectionObserver = new IntersectionObserver(debounce(handleIntersection, 500), { threshold: 0.5 });
    return intersectionObserver;
}

const handleIntersection = (entries) => {
    for (const entry of entries) {
        const video = entry.target;
        if (mainVideo !== video || video.paused || video.muted || video.ended) {
            continue;
        }
        sendRuntimeMessage('page_video_intersection_changed', {
            isIntersecting: entry.isIntersecting,
            paused: video.paused,
            muted: video.muted,
            ended: video.ended,
            duration: isDisneyplus() ? getDisneyVideoDuration()?.toString() : video.duration?.toString()
        })


    }
}
export const handleFullscreenchange = () => {
    if (document.fullscreenElement && settings.fullScreenHideSidePanel) {
        //entered fullscreen
        if (sidePanelOpened) {
            registerOpenSidePanelEvents();
        }
        sendRuntimeMessage("page_enter_fullscreen");

    } else {
        //exited fullscreen
    }
}

export function isNetflix() {
    return window.location.href.includes("netflix.com");
}

export function isDisneyplus() {
    return window.location.href.includes("disneyplus.com");
}

export function getDisneyVideoDuration() {
    return Math.floor(document.querySelector('.controls__footer__progressWrapper.display-flex > div.progress-bar > div')?.getAttribute('aria-valuemax'))
}
