import { activeVideoEnterPictureInPicture, exitPictureInPicture, switchPictureInPicture } from "./pictureInPicture";
import { clearAllStorage, getAllLocalStorageItems, getAllSessionStorageItems } from "./pageStorage";
import { sendRuntimeMessage } from "../../core/browers/runtime";
import { initFloatBall, removeFloatBall, showFloatBall } from "./floatBall";
import { pageScrolledObserver, scrollToBottom, scrollToTop } from "./pageScroll";
import { delay } from "lodash-es";
import { handleTabChanged } from "./tabs";
import { updatePageBySiteConfigs } from "./updatePage";
import { createSidebar, removeSidebar } from "./sidebar";
import { enterDocumentPip, exitDocumentPip, initDocumentPip, togglePictureInPicture } from "./documentPictureInPicture";
import { pauseVideo, playbackRateChange, playVideo, seekVideo, switchVideoMute, videoMutationObserver, videoPlayingObserver, volumeVideo } from "./video";

export let settings = {};
export let siteConfigs = {};
export let tab = {};
export let sidePanelOpened = false;
export let frameId = null;
export let sidePanelPorts = new Map();

export const startup = async () => {
    await initSettings();

    videoMutationObserver();
    delay(() => {
        pageScrolledObserver();
        videoPlayingObserver();

    }, 1000);
};

export const initSettings = async () => {
    const response = await sendRuntimeMessage("get_settings");
    console.log("initSettings", response);
    if (response?.success && response.data?.settings) {
        settings = response.data.settings;
        siteConfigs = response.data.siteConfigs;
        tab = response.data.tab;
        frameId = response.data.frameId;
        if (siteConfigs) {
            updatePageBySiteConfigs(siteConfigs);
        }
    }
    const autoPip = response?.success && (response.data?.tabConfig?.enterPictureInPicture || siteConfigs?.autoEnterPictureInPicture)
    if (autoPip) {
        window.onload = (event) => {
            console.log("page is fully loaded");
            setTimeout(() => {
                if (settings?.advancedPipEnable) {
                    enterDocumentPip(true);
                } else {
                    activeVideoEnterPictureInPicture();
                }
            }, 1000)

        };

    }

    const sidePanelResponse = await sendRuntimeMessage("is_sidepanel_opened");
    console.log("sidePanelResponse", sidePanelResponse);
    sidePanelOpened = sidePanelResponse?.success && sidePanelResponse.data.is_sidepanel_opened === true;

    if (sidePanelResponse?.data && settings?.floatBall && frameId === 0) {
        sidePanelResponse.data.is_sidepanel_opened ? removeFloatBall() : showFloatBall();
    }

    if (sidePanelResponse?.data && settings?.sidebarEnabled && frameId === 0) {
        sidePanelResponse.data.is_sidepanel_opened ? removeSidebar() : createSidebar();
    }
    initDocumentPip();
};

export const processContentScriptsMessage = async (message, sender, sendResponse) => {
    try {
        switch (message.action) {
            case 'update_setting':
                settings[message.data.key] = message.data.value;
                if (message.data.key === "floatBall") {
                    initFloatBall();
                }
                if (message.data.key === "theme" || message.data.key === "sidebarEnabled" || message.data.key === "sidebarPosition") {
                    removeSidebar();
                    createSidebar();
                }
                sendResponse({ success: true });
                break;
            case 'update_site_config':
                siteConfigs[message.data.key] = message.data.value;
                updatePageBySiteConfigs();
                sendResponse({ success: true });
                break;
            case 'tabUpdated':
                handleTabChanged(message.data.tabId, message.data.changeInfo, message.data.tab);
                sendResponse({ success: true });
                break;
            case 'tab_url_updated':
                await startup();
                sendResponse({ success: true });
                break;
            case 'save_tab_session': {
                const localStorageItems = getAllLocalStorageItems();
                const sessionStorageItems = getAllSessionStorageItems();
                sendResponse({ localStorageItems, sessionStorageItems });
                break;
            }
            case 'clear_browsing_data':
                clearAllStorage();
                sendResponse({ success: true });
                break;
            case 'play_video':
                playVideo();
                sendResponse({ success: true });
                break;
            case 'pause_video':
                pauseVideo();
                sendResponse({ success: true });
                break;
            case 'video_jump_to_time':
                seekVideo(message.data.value);
                sendResponse({ success: true });
                break;
            case 'volume_change':
                volumeVideo(message.data.value);
                sendResponse({ success: true });
                break;
            case 'playback_rate_change':
                playbackRateChange(message.data.value);
                sendResponse({ success: true });
                break;
            case 'video_switch_mute':
                switchVideoMute();
                sendResponse({ success: true });
                break;
            case 'switch_pip':
                if (settings?.advancedPipEnable) {
                    togglePictureInPicture();
                } else {
                    switchPictureInPicture();
                }
                sendResponse({ success: true });
                break;
            case 'enterPictureInPicture':
                if (settings?.advancedPipEnable) {
                    enterDocumentPip(true);
                } else {
                    activeVideoEnterPictureInPicture();
                }
                sendResponse({ success: true });
                break;
            case 'exitPictureInPicture':
                if (settings?.advancedPipEnable) {
                    exitDocumentPip();
                } else {
                    exitPictureInPicture()
                }
                sendResponse({ success: true });
                break;
            case 'openPopup':
                await sendRuntimeMessage("update_setting", { key: "clickExtensionIconAction", value: 'openPopup' });
                sendResponse({ success: true });
                break;
            case 'sidepanel_opened':
                if (message.data?.name === `sidepanel_${tab?.windowId}`) {
                    removeFloatBall();
                    removeSidebar();
                    sidePanelOpened = true;
                }
                unregisterOpenSidePanelEvents();
                if (!settings) {
                    await startup();
                }
                sendResponse({ success: true });
                break;
            case 'sidepanel_closed':
                sidePanelOpened = false;
                if (settings?.floatBall && message.data?.name === `sidepanel_${tab?.windowId}` && frameId === 0) {
                    showFloatBall();
                }
                if (settings?.sidebarEnabled && message.data?.name === `sidepanel_${tab?.windowId}` && frameId === 0) {
                    createSidebar();
                }
                sendResponse({ success: true });
                break;
            case 'scroll_to_top':
                scrollToTop();
                sendResponse({ success: true });
                break;
            case 'scroll_to_bottom':
                scrollToBottom();
                sendResponse({ success: true });
                break;
            case 'redirect_url':
                window.location.href = message.data.url
                sendResponse({ success: true });
                break
            default:
                sendResponse({ success: false, error: 'Unknown action' });
                break;
        }
    } catch (e) {
        console.error("runtime got error", { err: e, errmsg: e.message });
        try {
            sendResponse({ success: false, error: e.message });
        } catch (error) {
            console.error("send error Response error", { err: error, errmsg: error.message });
        }
    }
};

export const openSidePanel = async () => {
    await sendRuntimeMessage("open_sidepanel");
};

export const registerOpenSidePanelEvents = () => {
    const events = ["keydown", "mousedown", "pointerdown", "pointerup", "touchend"];
    events.forEach(event => document.addEventListener(event, openSidePanel, { once: true }));
};

export const unregisterOpenSidePanelEvents = () => {
    const events = ["keydown", "mousedown", "pointerdown", "pointerup", "touchend"];
    events.forEach(event => document.removeEventListener(event, openSidePanel, { once: true }));
};