
import { startup } from "./runtime";
import { videoMutationObserver, videoPlayingObserver } from "./video";



export const handleTabChanged = (tabId, changeInfo, tab) => {

    console.log(tabId, changeInfo, tab);

    if (('audible' in changeInfo) || ('mutedInfo' in changeInfo)) {
        videoPlayingObserver();
        videoMutationObserver();

        // pageScrolledObserver();
    }

    if ('status' in changeInfo) {
        switch (changeInfo.status) {
            case 'loading':
                break;
            case 'complete':
                startup();
                break;
        }

    }



}